import PhoneInput from 'react-phone-input-2';
import './App.css';
import 'react-phone-input-2/lib/style.css'
import { Button, TextField } from '@mui/material';
import { useState } from 'react';
import { RecaptchaVerifier, signInWithPhoneNumber } from 'firebase/auth';
import {auth} from './firebase.config'
function App() {
  
  const [phone,setPhone]=useState("")
  const [user,setUser]=useState(null)
  const [otp,setOtp]=useState("")

  const sendOtp=async()=>{
  try{
  const recaptcha=new RecaptchaVerifier(auth,"recaptcha",{});
  console.log(phone);
  const confirmation=await signInWithPhoneNumber(auth,phone,recaptcha)
  console.log(confirmation);
  setUser(confirmation)
  }catch(err){
    console.log(err);
  }
}

const verifyOTP=async()=>{
  console.log(otp);
  try{
 const data=await user.confirm(otp)
 console.log(data);
 alert("OTP VRIFIED SUCCESSFULLY..😍😍😍😍")
 setPhone("")
 setOtp("")
  }catch(err){
    console.log(err)
  }
}

  return(
    <div className='phone-signin'>
    <div className='phone-content'>
      <PhoneInput 
      country={"in"}
       value={phone} 
       onChange={(phone)=>setPhone("+" + phone)}
       placeholder='Enter phone number'
       />
       <Button onClick={sendOtp} variant='contained' sx={{marginTop:"10px"}}>Send OTP</Button> <br />
    <div style={{marginTop:"10px"}} id='recaptcha'></div>
    <TextField onChange={(e)=>setOtp(e.target.value)} sx={{marginTop:"10px",width:"300px"}} variant='outlined' size='small' label="enter OTP"/>
   <br />
    <Button onClick={verifyOTP} variant='contained' sx={{marginTop:"10px"}} color='success'>Verify OTP</Button>
    </div>
    </div>
  );
}

export default App;
